@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
strong {
  font-size: 20px;
  color: #333;
}

form {
  margin-top: 30px;
}

form .input-block + .input-block {
  margin-top: 20px;
}

form .input-group {
  margin-top: 20px;
  display: grid;
  grid-gap: 20px;
  gap: 20px;
  grid-template-columns: 1fr 1fr;
}

form .input-group .input-block {
  margin-top: 0;
}

form .input-block label {
  color: #acacac;
  font-size: 14px;
  font-weight: bold;
  display: block;
}

form .input-block input {
  width: 100%;
  height: 32px;
  font-size: 14px;
  color: #666;
  border: 0;
  border-bottom: 1px solid #eee;
}

form button[type="submit"] {
  width: 100%;
  border: 0;
  margin-top: 30px;
  background: #7d40e7;
  padding: 15px 20px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  transition: background 0.5s;
}

form button[type="submit"]:hover {
  background: #6932ca;
}
.dev-item {
  background: #fff;
  box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
  border-radius: 2px;
  padding: 20px;

  cursor: pointer;
}

.dev-item:hover {
  background: #f5f1f1;
}

.dev-item img {
  width: 54px;
  height: 54px;
  border-radius: 50%;
}

.dev-item > div {
  display: flex;
}

.dev-info {
  margin-left: 15px;
  display: flex;
  flex-direction: column;

  justify-content: center;
}

.dev-info span {
  color: #7d7d7d;
}




.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 30px;

  display: flex;
  align-items: flex-start;
}

.app main {
  margin-left: 30px;
}

aside {
  width: 320px;
  background: #fff;
  box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
  border-radius: 2px;
  padding: 30px 20px;
}

aside > p {
  text-align: center;
  color: red;
  margin-top: 10px;
}

main {
  flex: 1 1;
}

main ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  gap: 20px;
  list-style: none;
}
#profile-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#back-icon{
  position: absolute;
  top: 30px;
  left: 30px;
  width: 35px;
  height: 35px;
  cursor: pointer;
}

#profile-content {
  max-width: 900px;
  flex: 1 1;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
}

#profile-content h1 {
  margin-top: 20px;
  margin-bottom: 10px;
}

#profile-content > p {
  color: #525252;
  margin-bottom: 30px;
}

#profile-content img {
  height: 200px;
  width: 200px;
  border-radius: 50%;
}

#profile-infos {
  border: 1px solid #525252;
  display: flex;
  color: #525252;
  padding: 10px;
  width: 350px;
  justify-content: space-around;
}

.infos {
  flex: 1 1;
  padding-left: 10px;
}

.infos p {
  margin-bottom: 5px;
  padding: 2px;
}

.infos strong {
  font-size: 18px;
}

.left {
  border-left: 1px solid black;
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  background: #e5e6f0;
  -webkit-font-smoothing: antialiased;
}

body,
input,
button {
  font-family: Roboto, sans-serif;
}

a, a:visited, a:active {
  color: inherit;
}

